import logo from './logo.svg';
import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';
var brand;
class Car extends React.Component {
  constructor(name){
    super();
    this.brand = name;
  }
  render(){
    return(
      <div>
      <p>I am a car which is {this.props.brandname} extendenting react component.</p>
      <p id="change"></p>
      <button onClick={this.changeCar}>Change</button>
      </div>
    );
  }
  changeCar(){
    return(
      document.getElementById('change').innerHTML = "Changed !"
    );
  }
  present() {
    return 'I have a ' + this.brand;
  }
  
}
var mycar = new Car("Ford");


function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Hello World . 
          <Car brandname="Rolls Royce" />
          Car : {mycar.present()} <br/>
          Author : Sahil 
        </p>
        <a
          className="App-link"
          href="https://github.com/Sahil1709"
          target="_blank"
          rel="noopener noreferrer"
        >
          My Github
        </a>
      </header>
    </div>
  );
}

export default App;
